.js-sticky-scroll-catch {
  align-self: flex-start;
}
.sticky-scroll-catch--scrolled {
  position: fixed;
  bottom: 0;
}
.sticky-scroll-catch--break {
  position: absolute;
  bottom: 0;
}
.sticky-scroll-catch--downscroll,
.sticky-scroll-catch--upscroll {
  position: absolute;
}
.sticky-scroll-catch--scrolled-top {
  position: fixed;
  top: 0;
}
